import UDToast from './ud-toast'
export default {

    network(request) {
        let url = request.url;
        let params = request.params;
        let showLoading = request.showLoading;
        let method = request.method ? request.method : 'GET';
        showLoading && UDToast.showLoading();
        return  new Promise((resolve,reject)=>{
           fetch('https://eshop.turboradio.cn/api/cart/getCart',{
                method: method,
                body: params,
               headers: {
                   'Authorization' : "Bearer " + sessionStorage.getItem('jwt')
                   //eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..RUR87Hrx_MvgDkUtSzMNddBOqU43e0n_KIBnXPp7p5M
               }
            }).then(res => {
                showLoading && UDToast.hiddenLoading();
                showLoading && res.status != 200 && UDToast.showError(JSON.stringify(res));
                res.json().then(data => {
                    console.log(123123);
                    if (data.errno != '0') {
                        showLoading && UDToast.showError(data.error);
                        reject(data);
                    }else {
                        resolve(data.data);
                    }
                })
           }).catch(error => {
               showLoading && UDToast.hiddenLoading();
                showLoading && UDToast.showError('服务器请求失败');
                reject(error);
           })
        })
    },


    getData(url ,params={},showLoading=true) {
        return this.network({
            url:url,
            params:params,
            showLoading:showLoading,
            method: 'GET'
        });
    },
    postData(url ,params,showLoading=true) {
        return this.network({
            url:url,
            params:params,
            showLoading:showLoading,
            method: 'POST'
        });
    },
    /**
     *
     * @param requests
     *
     * requests格式：  [{url:'/order',params:{},method:'GET'},{url:'/user',params:{},method:'POST'}] method默认POST
     *
     */
    multiFetchData(requests) {
        if (!requests || requests.length == 0) {
            UDToast.showError('request无效');
        }
        UDToast.showLoading();
        Promise.all(requests.map(item => {
            return this.network({
                url: item.url,
                params:item.params,
                showLoading:false,
                method:item.method ? item.method : 'POST'
            });
        })).then(res=> {
            UDToast.hiddenLoading();
            console.log(12,res);
        }).catch(error=> {
            const err = error.error ? error.error : '服务器请求失败'
            UDToast.showError(err)
            UDToast.hiddenLoading();

        })
    }

}



