import React from 'react';
import './TabItem.css';
import {Link} from "react-router-dom";


class TabItem extends React.Component{
    render() {
        return (
            <Link to={this.props.tabId} className={`tab-item ${this.props.tabClass}`}  onClick={this.props.tabEvent}>
                <div>
                    <img className="tab-item-icon" src={this.props.tabUrl} />
                    <span className={`tab-item-name ${this.props.tabClass}`}>
                        {this.props.tabName}
                    </span>
                </div>
            </Link>
        );
    }
}
export default TabItem;
