import React from 'react';
import './Tabbar.css';
import TabItem from './TabItem/TabItem';

class Tabbar extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            tabs: [
                {tabName:'首页',id:'home'},
                {tabName:'清单列表',id:'goods'},
                {tabName:'我的',id:'mine'},
            ],
            current: this.props.current,
        };
    }
    itemNav(index) {
        this.setState({
            current: index
        })


    }
    render() {
        var tabLists = this.state.tabs.map((item,index) => {
            const tabUrl = index == this.state.current ? (require('../../static/images/tabbar/' + item.id + '_select.png')) : (require('../../static/images/tabbar/' + item.id + '.png'));
            const tabClass = index == this.state.current ? 'tab-active' : '';
            return (
                <TabItem key={index} tabId={item.id}  tabClass={tabClass} tabUrl={tabUrl} tabName={item.tabName} tabEvent={ this.itemNav.bind(this,index) }></TabItem>
            );
        });
        return (
            <div className="tab">
                {tabLists}
            </div>
        );
    }
}
export default Tabbar;
